import { createReducer, on } from "@ngrx/store";
import { IStoreEarnings } from "src/app/interfaces/earnings";
import { EarningsPersonLoad, EarningsPersonFail, EarningsPersonMonth, EarningsPersonSuccess, EarningsPersonWeek, EarningsPersonYear, SetFiltersEarnings, SetShowChartFiltersEarnings, ResetFiltersEarnings, CreateEarningSuccess, DeleteEarningSuccess, SetFiltersEarningsInitial, updateFiltersPaginatorEarnings, resetPageFilters, EarningsReset } from "../actions/earnings.actions";

const initialState: IStoreEarnings = {
    loaded: false,
    loading: false,
    data: [],
    complete: [],
    error: {},
    quantity: 0,
    pages: 0,
    year: 0,
    month: 0,
    week: 0,
    filters: {
        showChart: false,
    },
};

const _earningsReducer = createReducer(
    initialState,
    on(EarningsReset, (state, action) => initialState ),
    on(EarningsPersonLoad,
        (state, action) => {
            return {
                ...state,
                data: [],
                complete: [],
                error: {},
                loaded: false,
                loading: true,
            }
        },
    ),
    
    on(EarningsPersonSuccess,
        (state, action) => {
            let pagesArray = [];
            if(action?.pages){
                for (let i = 0; i < action?.pages; i++) {
                    pagesArray.push(i);
                }
            }
            return {
                ...state,
                data: action.earnings?.rows,
                complete: action?.complete,
                total: action.total,
                page: action.page,
                pages: action.pages,
                limit: action.limit,
                pagesArray,
                quantity: action.quantity,
                loaded: true,
                loading: false,
                filters: {
                    ...state.filters,
                    quantity: action.quantity,
                    total: action.total,
                    page: action.page,
                    pages: action.pages,
                    limit: action.limit,
                    pagesArray,
                }
            }
        },
    ),
    
    on(EarningsPersonFail,
        (state, action) => {
            return {
                ...state,
                data: [],
                complete: [],
                total: 0,
                error: action.error,
                loaded: true,
                loading: false,
            }
        },
    ),

    on(EarningsPersonYear,
        (state, action) => ({
            ...state,
            year: action?.total
        })
    ),

    on(EarningsPersonMonth,
        (state, action) => ({
            ...state,
            month: action?.total
        })
    ),

    on(EarningsPersonWeek,
        (state, action) => ({
            ...state,
            week: action?.total
        })
    ),

    on(SetFiltersEarnings,
        (state, action) => {
            // console.warn(action.filters);
            const newFilters = { ...action?.filters };

            if(newFilters?.showChart === 'true'){
                newFilters.showChart = true;
            }
            else if(newFilters?.showChart === 'false'){
                newFilters.showChart = false;
            }
            else if(!newFilters?.showChart){
                newFilters.showChart = false;
            }

            return {
            ...state,
            filters: {
                ...state.filters,
                ...newFilters
            }
        }}
    ),

    on(SetFiltersEarningsInitial,
        (state, action) => {
            // console.warn('set filter initial', action);
            const filters = { ...action?.filters };
            
            if(filters?.showChart === 'true'){
                filters.showChart = true;
            }
            else if(filters?.showChart === 'false'){
                filters.showChart = false;
            }

            return {
            ...state,
                filters: {
                    ...state.filters,
                    ...filters
                }
            }
        }
    ),

    on(ResetFiltersEarnings,
        (state, action) => ({
            ...state,
            filters: {
                showChart: false,
            }
        })
    ),
    
    on(updateFiltersPaginatorEarnings,
        (state, action) => ({
            ...state,
            filters: {
                ...state.filters,
                ...action.filters
            }
        })
    ),

    on(resetPageFilters,
        state => ({
            ...state,
            filters: {
                ...state.filters,
                page: 0,
                pages: 0,
                pagesArray: [],
                quantity: 0
            }
        })
    ),

    on(SetShowChartFiltersEarnings,
        (state, action) => {
            // console.error(state.filters);
            return ({
            ...state,
            filters: {
                ...state.filters,    
                showChart: !state?.filters?.showChart
            }
        })
    }
    ),

    on(CreateEarningSuccess,
        (state, action) => ({
            ...state,
            month: 0,
            year: 0,
        })
    ),

    on(DeleteEarningSuccess,
        (state, action) => ({
            ...state,
            month: 0,
            year: 0,
        })
    ),
);

export function earningsReducer(state: IStoreEarnings, action: any){
    return _earningsReducer(state, action);
}