import { createAction, props } from "@ngrx/store";

export const Login = createAction(
  '[User] Login'
);
export const LogOut = createAction(
  '[User] LogOut'
);
export const LoginSuccess = createAction(
  '[User] LoginSuccess',
  props<any>()
);
export const Register = createAction(
  '[User] Register'
);
export const RegisterSuccess = createAction(
  '[User] Register Success',
  props<any>()
);
export const RegisterFail = createAction(
  '[User] Register Fail',
  props<any>()
);
export const SetDataPerson = createAction(
  '[User] SetDataPerson',
  props<any>()
);
export const DataPerson = createAction(
  '[User] DataPerson',
  props<any>()
);
export const LoginFail = createAction(
  '[User] LoginFail',
  props<any>()
);
export const PayMethods = createAction(
  '[User] PayMethods',
  props<{payMethods: any}>()
);
export const Accounts = createAction(
  '[User] Accounts',
  props<{accounts: any}>()
);

export const HeritagePerson = createAction(
  '[User] HeritagePerson',
  props<{heritages: any}>()
);

export const HeritageUpdateStates = createAction(
  '[User] UpdateStatesHeritage'
);