import { createAction, props } from "@ngrx/store";
import { IExpensivesResponse, IFiltersExpensives } from "src/app/interfaces/http/expensives-http.type";

// Actions
export const CreateExpensivesSuccess = createAction('[Expensives User] Create Success');
export const DeleteExpensivesSuccess = createAction('[Expensives User] Delete Success');

// General Data
export const ExpensivesReset = createAction('[Expensives User] Reset');
export const ExpensivesPersonLoad = createAction('[Expensives User] Load');
export const ExpensivesPersonSuccess = createAction( '[Expensives User] Success', props<IExpensivesResponse>() );
export const ExpensivesPersonFail = createAction( '[Expensives User] Fail', props<{error: any}>() );

// Data Global
export const ExpensivePersonYear = createAction('[Expensives User] Year', props<{ total: number }>());
export const ExpensivePersonMonth = createAction('[Expensives User] Month', props<{ total: number }>());
export const ExpensivePersonWeek = createAction('[Expensives User] Week', props<{ total: number }>());
export const ExpensivePersonToday = createAction('[Expensives User] Hoy', props<{ total: number }>());
export const ExpensivePersonYesterday = createAction('[Expensives User] Yesterday', props<{ total: number }>());

// Filters
export const RestoreFiltersExpensives = createAction( '[Expensives User] Restore Filters Expensives', props<{filters: IFiltersExpensives, ref?:string}>() );
export const SetFiltersExpensivesInitial = createAction( '[Expensives User] Set Filters Initial', props<{filters: IFiltersExpensives, ref:string}>() );
export const SetFiltersExpensives = createAction( '[Expensives User] Set Filters Expensives', props<{filters: IFiltersExpensives, ref:string}>() );
export const updateFiltersPaginatorExpensives = createAction( '[Expensives User] Update Filters Paginator Expensives', props<{filters: IFiltersExpensives, ref:string}>() );
export const resetPageFilters = createAction( '[Expensives User] Reset Filters Page Expensives' );
export const ResetFiltersExpensives = createAction( '[Expensives User] Reset Filters Expensives' );
export const SetShowChartFiltersExpensives = createAction( '[Expensives User] Set Show Chart Filters Expensives' );