import { createReducer, on } from "@ngrx/store";
import { InvestmentsPersonLoad, InvestmentsPersonFail, InvestmentsPersonMonth, InvestmentsPersonSuccess, InvestmentsPersonWeek, InvestmentsPersonYear, SetFiltersInvestments, SetShowChartFiltersInvestments, ResetFiltersInvestments, CreateInvestmentSuccess, DeleteInvestmentSuccess, SetFiltersInvestmentsInitial, updateFiltersPaginatorInvestments, resetPageFilters, InvestmentsReset } from "../actions/investments.actions";
import { IStoreInvestments } from "src/app/interfaces/investments";

const initialState: IStoreInvestments = {
    loaded: false,
    loading: false,
    data: [],
    complete: [],
    error: {},
    quantity: 0,
    pages: 0,
    year: 0,
    month: 0,
    week: 0,
    filters: {
        showChart: false,
    },
};

const _investmentsReducer = createReducer(
    initialState,
    on(InvestmentsReset, (state, action) => initialState ),
    on(InvestmentsPersonLoad,
        (state, action) => {
            return {
                ...state,
                data: [],
                complete: [],
                error: {},
                loaded: false,
                loading: true,
            }
        },
    ),
    
    on(InvestmentsPersonSuccess,
        (state, action) => {
            let pagesArray = [];
            if(action?.pages){
                for (let i = 0; i < action?.pages; i++) {
                    pagesArray.push(i);
                }
            }
            return {
                ...state,
                data: action.investments?.rows,
                complete: action?.complete,
                total: action.total,
                page: action.page,
                pages: action.pages,
                limit: action.limit,
                pagesArray,
                quantity: action.quantity,
                loaded: true,
                loading: false,
                filters: {
                    ...state.filters,
                    quantity: action.quantity,
                    total: action.total,
                    page: action.page,
                    pages: action.pages,
                    limit: action.limit,
                    pagesArray,
                }
            }
        },
    ),
    
    on(InvestmentsPersonFail,
        (state, action) => {
            return {
                ...state,
                data: [],
                complete: [],
                total: 0,
                error: action.error,
                loaded: true,
                loading: false,
            }
        },
    ),

    on(InvestmentsPersonYear,
        (state, action) => ({
            ...state,
            year: action?.total
        })
    ),

    on(InvestmentsPersonMonth,
        (state, action) => ({
            ...state,
            month: action?.total
        })
    ),

    on(InvestmentsPersonWeek,
        (state, action) => ({
            ...state,
            week: action?.total
        })
    ),

    on(SetFiltersInvestments,
        (state, action) => {
            // console.warn(action.filters);
            const newFilters = { ...action?.filters };

            if(newFilters?.showChart === 'true'){
                newFilters.showChart = true;
            }
            else if(newFilters?.showChart === 'false'){
                newFilters.showChart = false;
            }
            else if(!newFilters?.showChart){
                newFilters.showChart = false;
            }

            return {
            ...state,
            filters: {
                ...state.filters,
                ...newFilters
            }
        }}
    ),

    on(SetFiltersInvestmentsInitial,
        (state, action) => {
            // console.warn('set filter initial', action);
            const filters = { ...action?.filters };
            
            if(filters?.showChart === 'true'){
                filters.showChart = true;
            }
            else if(filters?.showChart === 'false'){
                filters.showChart = false;
            }

            return {
            ...state,
                filters: {
                    ...state.filters,
                    ...filters
                }
            }
        }
    ),

    on(ResetFiltersInvestments,
        (state, action) => ({
            ...state,
            filters: {
                showChart: false,
            }
        })
    ),
    
    on(updateFiltersPaginatorInvestments,
        (state, action) => ({
            ...state,
            filters: {
                ...state.filters,
                ...action.filters
            }
        })
    ),

    on(resetPageFilters,
        state => ({
            ...state,
            filters: {
                ...state.filters,
                page: 0,
                pages: 0,
                pagesArray: [],
                quantity: 0
            }
        })
    ),

    on(SetShowChartFiltersInvestments,
        (state, action) => {
            // console.error(state.filters);
            return ({
            ...state,
            filters: {
                ...state.filters,    
                showChart: !state?.filters?.showChart
            }
        })
    }
    ),

    on(CreateInvestmentSuccess,
        (state, action) => ({
            ...state,
            month: 0,
            year: 0,
        })
    ),

    on(DeleteInvestmentSuccess,
        (state, action) => ({
            ...state,
            month: 0,
            year: 0,
        })
    ),
);

export function investmentsReducer(state: IStoreInvestments, action: any){
    return _investmentsReducer(state, action);
}