import { createReducer, on } from '@ngrx/store';
import { IUserData } from 'src/app/interfaces/user';
import { Accounts, DataPerson, Login, LoginFail, LoginSuccess, LogOut, HeritagePerson, PayMethods, SetDataPerson } from '../actions/user';
export interface IUserState {
  loading: boolean;
  loaded: boolean;
  data: IUserData;
  error: any;
};

const initialState: IUserState = {
  loaded: false,
  loading: false,
  data: {
    expensives: {
      year: 0,
      month: 0,
      total: 0,
      week: 0,
      today: 0,
      yesterday: 0,
    },
    earnings: {
      year: 0,
      month: 0,
      total: 0,
      week: 0,
    },
    investments: {
      year: 0,
      month: 0,
      total: 0,
      week: 0,
    }
  },
  error: {},
};

const _userReducer = createReducer(
  initialState,
  on(Login,
    (state) => ({
      ...state, 
      loading: true, 
      loaded: false,
      data: {},
      error: {},
    }),
  ),
  on(LoginSuccess,
    (state, action) => {
      localStorage.login = true;
      localStorage.user = JSON.stringify({...state.data, ...action.any });
      return {
        ...state,
        data: {
          ...state.data,
          ...action.any,
        },
        loading: false,
        loaded: true,
        error: {}
      }
    },
  ),
  on(SetDataPerson,
    (state, action) => {
      localStorage.user = JSON.stringify({
        ...state.data,
        person: {
          ...state.data.person,
          ...action.any
        }
      });
      return {
        ...state,
        data: {
          ...state.data,
          person: {
            ...state.data.person,
            ...action.any
          }
        }
      }
    },
  ),
  on(DataPerson,
    (state, action) => {
      localStorage.user = JSON.stringify({
        ...state.data,
        person: action.any
      });
      return {
        ...state,
        data: {
          ...state.data,
          person: action.any,
        }
      }
    },
  ),
  on(PayMethods,
    (state, action) => {
      return {
        ...state,
        data: {
          ...state.data,
          payMethods: action.payMethods
        }
      }
    },
  ),
  on(Accounts,
    (state, action) => {
      return {
        ...state,
        accounts: action.accounts,
        data: {
          ...state.data,
          accounts: action.accounts
        }
      }
    },
  ),
  on(HeritagePerson,
    (state, action) => {
      return {
        ...state,
        data: {
          ...state.data,
          heritages: {
            data: action.heritages.heritages,
            total: action.heritages.total
          }
        }
      }
    },
  ),
  on(LogOut,
    (state, action) => {
      delete localStorage.login;
      delete localStorage.user;
      return {
        ...state,
        data: {},
        loading: false,
        loaded: false,
        error: {}
      }
    },
  ),
  on(LoginFail,
    (state, action) => {
      return {
        ...state,
        data: {},
        loading: false,
        loaded: true,
        error: action.any
      }
    },
  ),
);

export function userReducer(state: IUserState, action: any){
  return _userReducer(state, action);
}