import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { defaultIdError } from './user.service';
import { environment } from 'src/environments/environment';
import { ICreateEarningsResponse, IDeleteEarningResponse, IEarningsResponse } from '../interfaces/http/earnings-http.type';
import { buildUrl, currentDateInput } from 'src/environments/global';

@Injectable({
  providedIn: 'root'
})
export class EarningsService {
  constructor(private http: HttpClient) { }

  earnings(id: string): Promise<IEarningsResponse>{
    console.warn('b->earnings()');
    if(!id){ 
      console.warn('earnings()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}earnings-person/${id}`).toPromise();
  }

  getDataEarning(id: any){
    return this.http.get(`${environment.urlBackend}earning/${id}`, {}).toPromise();
  }

  getEarningsByAccount(data: any){
    return this.http.get(`${environment.urlBackend}earnings-account?account=${data.account}&person=${data.person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
  }

  getEarningsByCategory(data: any){
    return this.http.get(`${environment.urlBackend}category-earnings?category=${data?.category}&person=${data?.person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
  }

  getEarningsByDate(data: any, ref: string){
    // console.log('getEarningsByDate', ref);
    console.warn('b->getEarningsByDate()');
    if(!data.id_person){ 
      console.warn('getEarningsByDate()');
      return defaultIdError;
    }
    if(!data?.page) data.page = 0;
    if(data?.type === 'today'){ data.date =currentDateInput(new Date()); }

    if(data.pagesArray) delete data.pagesArray;
    if(data.pages) delete data.pages;
    if(data.total) delete data.total;
    if(data.quantity) delete data.quantity;

    const url = buildUrl(`${environment.urlBackend}earnings-date`, data);
    return this.http.get(url, data).toPromise(); }

  createOrUpdateEarnings(id: string, data: any): Promise<ICreateEarningsResponse>{
    return this.http.post(`${environment.urlBackend}create-update-earning/${id}`, data).toPromise();
  }

  deleteEarnings(data: any): Promise<IDeleteEarningResponse>{
    return this.http.post(`${environment.urlBackend}delete-earning`, data).toPromise();
  }

  categoriesEarnings(id: string){
    console.warn('b->categoriesEarnings()');
    if(!id) { 
      console.warn('categoriesEarnings()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}category-earnings/${id}`).toPromise();
  }
  
  getValuesDataCategories(id: string) {
    return this.http.get(`${environment.urlBackend}category-earnings-values/${id}`).toPromise();
  }

  createCategoryEarnings(data: any){
    return this.http.post(`${environment.urlBackend}create-category-earnings`, data).toPromise();
  }

  deleteCategorieEarning(data: any){
    return this.http.post(`${environment.urlBackend}delete-category-earnings`, data).toPromise();
  }
}
