export function currentDateInput(date?: string | Date): string {
  if(!date){
    date = new Date();
  }
  let parts: any = [];
  parts = new Date(date).toLocaleString().split(',')[0].split('/');

  if(navigator.language === 'en-US'){
    if(parseInt(parts[0]) < 10){
      parts[0] = '0' + parts[0];
    }
    if(parseInt(parts[1]) < 10){
      parts[1] = '0' + parts[1];
    }
  
    parts.reverse().join('-');
    if(date){
      return `${parts[0]}-${parts[1]}-${parts[2]}`;
      // return `${parts[0]}-${parts[2]}-${parts[1]}`;
    }
    
    return `${parts[0]}-${parts[1]}-${parts[2]}`;
    // return `${parts[0]}-${parts[2]}-${parts[1]}`;
  }else{
    const v0 = parseInt(parts[0]);
    const v1 = parseInt(parts[1]);

    if(v1 < 10){
      parts[0] = '0' + v1;
    }else{
      parts[0] = v1;
    }
    if(v0 < 10){
      parts[1] = '0' + v0;
    }else{
      parts[1] = v0;
    }
  
    parts.reverse().join('-');
    if(date){
      return `${parts[0]}-${parts[2]}-${parts[1]}`;
    }
  
    return `${parts[0]}-${parts[2]}-${parts[1]}`;
  }
  
};

export function buildUrl(url: string, data: any) {
  const queryParams = Object.entries(data)
      .filter(([key, value]) => value !== undefined && value !== null && value !== '')
      .map(([key, value]) => `${key}=${value}`)
      .join('&');

  return `${url}?${queryParams}`;
}

export function dialogSmall(
  text: string = '',
  icon: SweetAlertIcon = 'success',
  timer: number = 2000,
) {
  Swal.fire({
    text,
    icon,
    timer,
    position: 'bottom-end',
    customClass: 'popupSmall',
    backdrop: false,
    timerProgressBar: true,
    showConfirmButton: false
  });
}