import { createReducer, on } from '@ngrx/store';
import { CategoriesEarnings } from '../actions/categories_earnings';
import { ICategoryEarning } from 'src/app/interfaces/categories_earnings';

export interface ICategoryEarningState {
  loading: boolean;
  loaded: boolean;
  data: ICategoryEarning[];
  error: any;
}

const initialState: ICategoryEarningState = {
  loaded: false,
  loading: false,
  data: [],
  error: {},
};

const _categoriesEarningsReducer = createReducer(
  initialState,
  on(CategoriesEarnings, (state, action) => ({
    ...state,
    loading: false,
    loaded: true,
    data: action.any,
    error: {},
  }))
);

export function categoriesEarningsReducer(state: ICategoryEarningState, action: any) {
  return _categoriesEarningsReducer(state, action);
}
