import { Injectable, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { IState } from '../store';
import { Subscription } from 'rxjs';
import { IUserData, IUserPerson } from '../interfaces/user';
import { UserService } from './user.service';
import { Accounts } from '../store/actions/user';
import { IDeleteEarningResponse, IEarningsResponse } from '../interfaces/http/earnings-http.type';
import { EarningsService } from './earnings.service';
import { EarningsPersonLoad, EarningsPersonSuccess, EarningsPersonFail, EarningsPersonYear, EarningsPersonMonth, DeleteEarningSuccess } from '../store/actions/earnings.actions';
import { CategoriesEarnings } from '../store/actions/categories_earnings';
import { IEarning } from '../interfaces/earnings';
import Swal from 'sweetalert2';
import { Router } from '@angular/router';
import { IDeleteInvestmentResponse, IInvestmentsResponse } from '../interfaces/http/investments-http.type';
import { DeleteInvestmentSuccess, InvestmentsPersonFail, InvestmentsPersonLoad, InvestmentsPersonMonth, InvestmentsPersonSuccess, InvestmentsPersonYear } from '../store/actions/investments.actions';
import { InvestmentsService } from './investments.service';
import { IInvestment } from '../interfaces/investments';
import { CategoriesInvestments } from '../store/actions/categories_investments';
import { DeleteExpensivesSuccess, ExpensivePersonMonth, ExpensivePersonToday, ExpensivePersonWeek, ExpensivePersonYear, ExpensivePersonYesterday, ExpensivesPersonFail, ExpensivesPersonLoad, ExpensivesPersonSuccess } from '../store/actions/expensives.actions';
import { IDeleteExpensiveResponse, IExpensivesResponse } from '../interfaces/http/expensives-http.type';
import { ExpensivesService } from './expensives.service';
import { CategoriesExpenses } from '../store/actions/categories_expenses';
import { IExpensive } from '../interfaces/expensives';
import { currentDateInput, dialogSmall } from 'src/environments/global';

const notincategory = '';
@Injectable({
  providedIn: 'root'
})
export class StoreService implements OnDestroy {
  sus: Subscription = new Subscription();
  data: any = {};
  user: IUserData = {};
  person: IUserPerson = {};
  
  constructor(
    private store: Store<IState>,
    private userService: UserService,
    private earningsSrv: EarningsService,
    private investmentsSrv: InvestmentsService,
    private expensivesSrv: ExpensivesService,
    private router: Router
  ) {
    this.sus = this.store.subscribe((state) => {
      this.data = state;
      this.user = state?.user;
      this.person = state?.user?.data?.person;
    });
  }

  // Getters
  get accounts(): any {
    return this.user?.accounts;
  }
  
  get categoriesEarnings(): any {
    return this.data?.categories_earnings?.data;
  }

  get categoriesInvestments(): any {
    return this.data?.categories_investments?.data;
  }

  get categoriesExpensives(): any {
    return this.data?.categories_expenses?.data;
  }
  
  get userD(): any {
    return this.user;
  }
  
  ngOnDestroy(): void {
    this.sus.unsubscribe();
  }

  // Accounts
  validateAccounts(): boolean {
    return this.user?.accounts?.length ? true : false;
  }

  async getAccountsUser(): Promise<void>{
    try {
      if(!this.validateAccounts()){
        let response: any = await this.userService.accounts(''+this.person?.id);
  
        if(response?.length){
          this.store.dispatch(Accounts({accounts: response}));
        }else{
          this.store.dispatch(Accounts({accounts: []}));
        }
      }
    } catch (error) {
      this.store.dispatch(Accounts({accounts: []}));
      console.log(error);
    }
  }

  // Earnings
  async earningsByDate(params: any, ref?: string){
    // console.warn('earningsByDate === ', params, ref);
    try {
      console.log('*********************************');
      console.log('debug earnings by date()');
      console.log('*********************************');
      // if is loading data, no search again
      if(this.data?.earnings?.loading && !this.data?.earnings?.data?.length){ return; }
      else if(this.data?.earnings?.loading){ debugger;return; }
      this.store.dispatch(EarningsPersonLoad());
      let response: IEarningsResponse = await this.earningsSrv.getEarningsByDate({...params, id_person: this.person?.id}, '***store earnings by date'+ref);
      // console.warn(response);
  
      if (response?.earnings && response?.earnings?.rows?.length >= 0) {
        this.store.dispatch(EarningsPersonSuccess({...response, ref: 'FROM STORE SERVICE'+ref}));
      }else{
        this.store.dispatch(EarningsPersonFail({ error: response }));
      }
    } catch (error) {
      console.log(error);
      this.store.dispatch(EarningsPersonFail({ error }));
    }
  }

  async getAllDataEarnings(): Promise<any> {
    const [yearData, monthData] = await Promise.all([
      this.year(),
      this.month(),
    ]);
  
    const dispatchYearTotal = yearData?.total || 0;
    const dispatchMonthTotal = monthData?.total || 0;
  
    this.store.dispatch(EarningsPersonYear({ total: dispatchYearTotal }));
    this.store.dispatch(EarningsPersonMonth({ total: dispatchMonthTotal }));
  };

  year(): Promise<IEarningsResponse> {
    if(this.data.earnings?.year){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.earnings?.year});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get earnings by current year
      try {
        const date = new Date();
        console.log('*********************************');
        console.log('year promise');
        console.log('*********************************');
        const params = {
          type: 'year',
          id_person: this.person?.id,
          year: date.getFullYear(),
          notincategory
        };
        let responsey: IEarningsResponse = await this.earningsSrv.getEarningsByDate(params, 'store year');
  
        resolve(responsey);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  month(): Promise<IEarningsResponse> {
    if(this.data.earnings?.month){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.earnings?.month});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get earnings by current month
      try {
        console.log('*********************************');
        console.log('month promise');
        console.log('*********************************');
        const date = new Date();
        const params = {
          type: 'month',
          id_person: this.person?.id,
          month: date.getFullYear() + '-' + (date.getMonth() + 1),
          notincategory
        };
        let response2: IEarningsResponse = await this.earningsSrv.getEarningsByDate(params, 'store month');
        resolve(response2);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  async deleteEarning(item: IEarning) {
    if (item.id) {
      Swal.fire({
        title: 'Delete ' + item?.description + '?',
        text: `Value: $${new Intl.NumberFormat().format(item.value)}`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Delete',
        showLoaderOnConfirm: true,
        preConfirm: async (res: any) => {
          if(!res) return;
  
          try {
            let response: IDeleteEarningResponse = await this.earningsSrv.deleteEarnings({...item});
    
            if (response.earning) {
              dialogSmall( 'Your Earning has been deleted.', 'success' );
              this.store.dispatch(DeleteEarningSuccess());
              if(this.router.url.includes('details')) this.router.navigateByUrl('/dashboard/earnings/create');
            } else {
              dialogSmall( 'Cannot delete the earning', 'error' );
              // console.log('ingreso eliminado fallida');
            }
          } catch (error:any) {
            Swal.fire({
              icon: 'error',
              title: 'Error',
              text: `Error: ${error?.error?.join(', ')}`,
              footer: 'Earning could not be deleted',
            });
          }
        }
      })
    }
  }

  async getCategoriesEarnings(){
    if(!this.categoriesEarnings?.length){
      try {
        console.log('*********************************');
        console.log('getCategoriesEarnings');
        console.log('*********************************');
        if(!localStorage.user) return;
        let response2: any = await this.earningsSrv.categoriesEarnings(JSON.parse(localStorage.user).person.id);
  
        if(response2.length){
          this.store.dispatch(CategoriesEarnings({any: response2}));
        }else{
          this.store.dispatch(CategoriesEarnings({any: []}));
        }
      } catch (error) {
        console.log('error en categories earnings', error);
        this.store.dispatch(CategoriesEarnings({any: []}));
      }
    }
  }

  // Investments
  async investmentsByDate(params: any, ref?: string){
    // console.warn('investmentsByDate === ', params, ref);
    try {
      // if is loading data, no search again
      if(this.data?.investments?.loading && !this.data?.investments?.data?.length){ return; }
      else if(this.data?.investments?.loading){ debugger;return; }
      this.store.dispatch(InvestmentsPersonLoad());
      let response: IInvestmentsResponse = await this.investmentsSrv.getInvestmentsByDate({...params, id_person: this.person?.id}, '***store investments by date'+ref);
      // console.warn(response);
  
      if (response?.investments && response?.investments?.rows?.length >= 0) {
        this.store.dispatch(InvestmentsPersonSuccess({...response, ref: 'FROM STORE SERVICE'+ref}));
      }else{
        this.store.dispatch(InvestmentsPersonFail({ error: response }));
      }
    } catch (error) {
      console.log(error);
      this.store.dispatch(InvestmentsPersonFail({ error }));
    }
  }

  async getAllDataInvestments(): Promise<any> {
    const [yearData, monthData] = await Promise.all([
      this.yearInvestment(),
      this.monthInvestment(),
    ]);
  
    const dispatchYearTotal = yearData?.total || 0;
    const dispatchMonthTotal = monthData?.total || 0;
  
    this.store.dispatch(InvestmentsPersonYear({ total: dispatchYearTotal }));
    this.store.dispatch(InvestmentsPersonMonth({ total: dispatchMonthTotal }));
  };

  yearInvestment(): Promise<IInvestmentsResponse> {
    if(this.data.investments?.year){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.investments?.year});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get investments by current year
      try {
        const date = new Date();
        const params = {
          type: 'year',
          id_person: this.person?.id,
          year: date.getFullYear(),
          notincategory
        };
        let responsey: IInvestmentsResponse = await this.investmentsSrv.getInvestmentsByDate(params, 'store year');
  
        resolve(responsey);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  monthInvestment(): Promise<IInvestmentsResponse> {
    if(this.data.investments?.month){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.investments?.month});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get investments by current month
      try {
        const date = new Date();
        const params = {
          type: 'month',
          id_person: this.person?.id,
          month: date.getFullYear() + '-' + (date.getMonth() + 1),
          notincategory
        };
        let response2: IInvestmentsResponse = await this.investmentsSrv.getInvestmentsByDate(params, 'store month');
        resolve(response2);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  async deleteInvestment(item: IInvestment) {
    if (item.id) {
      Swal.fire({
        title: 'Delete ' + item?.description + '?',
        text: `Value: $${new Intl.NumberFormat().format(item.value)}`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Delete',
        showLoaderOnConfirm: true,
        preConfirm: async (res: any) => {
          if(!res) return;
  
          try {
            let response: IDeleteInvestmentResponse = await this.investmentsSrv.deleteInvestment({...item});
    
            if (response.investment) {
              dialogSmall( 'Your Investment has been deleted.', 'success' );
              this.store.dispatch(DeleteInvestmentSuccess());
              if(this.router.url.includes('details')) this.router.navigateByUrl('/dashboard/investments/create');
            } else {
              dialogSmall( 'Cannot delete the investment.', 'error' );
              // console.log('ingreso eliminado fallida');
            }
          } catch (error:any) {
            Swal.fire({
              icon: 'error',
              title: 'Error',
              text: `Error: ${error?.error?.join(', ')}`,
              footer: 'Investment could not be deleted',
            });
          }
        }
      });
    }
  }

  async getCategoriesInvestments(){
    if(!this.categoriesInvestments?.length && localStorage.user){
      try {
        let response2: any = await this.investmentsSrv.categoriesInvestments(JSON.parse(localStorage.user).person.id);
  
        if(response2.length){
          this.store.dispatch(CategoriesInvestments({any: response2}));
        }else{
          this.store.dispatch(CategoriesInvestments({any: []}));
        }
      } catch (error) {
        console.log('error en categories Investments', error);
        this.store.dispatch(CategoriesInvestments({any: []}));
      }
    }
  }

  // Expensives
  async expensivesByDate(params: any, ref?: string){
    // console.warn('expensivesByDate === ', params, ref);
    try {
      console.log('*********************************');
      console.log('expensivesByDate = ');
      console.log('*********************************');
      // if is loading data, no search again
      if(this.data?.expensives?.loading && !this.data?.expensives?.data?.length){ return; }
      else if(this.data?.expensives?.loading){ debugger;return; }
      this.store.dispatch(ExpensivesPersonLoad());
      let response: IExpensivesResponse = await this.expensivesSrv.getExpensesByDate({...params, id_person: this.person?.id}, '***store expensives by date'+ref);
      // console.warn(response);
  
      if (response?.expensives && response?.expensives?.rows?.length >= 0) {
        this.store.dispatch(ExpensivesPersonSuccess({...response, ref: 'FROM STORE SERVICE'+ref}));
      }else{
        this.store.dispatch(ExpensivesPersonFail({ error: response }));
      }
    } catch (error) {
      console.log(error);
      this.store.dispatch(ExpensivesPersonFail({ error }));
    }
  }

  async getAllDataExpensives(): Promise<any> {
    console.warn('get all data expensives');
    const [yearData, monthData, weekData, todayData, yesterdaydata] = await Promise.all([
      this.yearExpensive(),
      this.monthExpensive(),
      this.weekExpensive(),
      this.todayExpensive(),
      this.yesterdayExpensive()
    ]);

    const dispatchYearTotal = yearData?.total || 0;
    const dispatchMonthTotal = monthData?.total || 0;
    const dispatchWeekTotal = weekData?.total || 0;
    const dispatchTodayTotal = todayData?.total || 0;
    const dispatchYesterdayTotal = yesterdaydata?.total || 0;
  
    this.store.dispatch(ExpensivePersonYear({ total: dispatchYearTotal }));
    this.store.dispatch(ExpensivePersonMonth({ total: dispatchMonthTotal }));
    this.store.dispatch(ExpensivePersonWeek({ total: dispatchWeekTotal }));
    this.store.dispatch(ExpensivePersonToday({ total: dispatchTodayTotal }));
    this.store.dispatch(ExpensivePersonYesterday({ total: dispatchYesterdayTotal }));
  };

  yearExpensive(): Promise<IExpensivesResponse> {
    console.warn('yearExpensive()');
    if(this.data.expensives?.year){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.expensives?.year});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get expensives by current year
      try {
        console.log('*********************************');
        console.log('year expensive promise');
        console.log('*********************************');
        const date = new Date();
        const params = {
          type: 'year',
          id_person: this.person?.id,
          year: date.getFullYear(),
          notincategory
        };
        let responsey: IExpensivesResponse = await this.expensivesSrv.getExpensesByDate(params, 'store year');
  
        resolve(responsey);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  monthExpensive(): Promise<IExpensivesResponse> {
    console.warn('monthExpensive()');
    if(this.data.expensives?.month){
      return new Promise((resolve, reject) => {
        resolve({total: this.data.expensives?.month});
      });
    }
    return new Promise(async (resolve, reject) => {
      // get expensives by current month
      try {
        console.log('*********************************');
        console.log('month expensive promise');
        console.log('*********************************');
        const date = new Date();
        const params = {
          type: 'month',
          id_person: this.person?.id,
          month: date.getFullYear() + '-' + (date.getMonth() + 1),
          notincategory
        };
        let response2: IExpensivesResponse = await this.expensivesSrv.getExpensesByDate(params, 'store month');
        resolve(response2);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  };

  weekExpensive(): Promise<any> {
    console.warn('weekExpensive()');
    return new Promise(async (resolve, reject) => {
      // get expensives by current week
      try {
        console.log('*********************************');
      console.log('week promise expensive');
      console.log('*********************************');
        const date = new Date();
        const params = {
          type: 'week',
          id_person: this.person?.id,
          week: date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate(),
          notincategory
        };
        let responsew: any = await this.expensivesSrv.getExpensesByDate(params, 'week');
    
        // console.warn('expensives by last seven day', responsew);
        resolve(responsew);
      } catch (error) {
        console.log('---------------------', error);
      }
    });
  }

  todayExpensive(): Promise<any> {
    console.warn('todayExpensive()');
    return new Promise(async (resolve, reject) => {
      // get expensives by current today
      try {
        console.log('*********************************');
      console.log('today expensive promise');
      console.log('*********************************');
        const date = currentDateInput();
        const params = {
          type: 'date',
          date,
          id_person: this.person?.id,
          notincategory
        };
        let response3: any = await this.expensivesSrv.getExpensesByDate(params, 'today');
  
        // console.warn('expensives by today', response3);
        resolve(response3);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  }

  yesterdayExpensive(): Promise<any> {
    console.warn('yesterdaExpensive()');
    return new Promise(async (resolve, reject) => {
      // get expensives by current yesterday
      try {
        console.log('*********************************');
      console.log('yesterdar exoensive promise');
      console.log('*********************************');
        var day = 60 * 60 * 24 * 1000;
        var startDate = new Date();
        const date = new Date(startDate.getTime() - day);
        const dateY = currentDateInput(new Date(date));

        const params = {
          type: 'date',
          id_person: this.person?.id,
          date: dateY,
          notincategory
        };
        let response4: any = await this.expensivesSrv.getExpensesByDate(params, 'yesterday');
    
        // console.warn('expensives by yesterday', response4);
        resolve(response4);
      } catch (error) {
        // console.log('---------------------', error);
      }
    });
  }

  async deleteExpensive(item: IExpensive) {
    if (item.id) {
      Swal.fire({
        title: 'Delete ' + item?.description + '?',
        text: `Value: $${new Intl.NumberFormat().format(item.value)}`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Delete',
        showLoaderOnConfirm: true,
        preConfirm: async (res: any) => {
          if(!res) return;
  
          try {
            let response: IDeleteExpensiveResponse = await this.expensivesSrv.deleteExpensive({...item});
    
            if (response.expensive) {
              dialogSmall( 'Your Expensive has been deleted.', 'success' );
              this.store.dispatch(DeleteExpensivesSuccess());
              if(this.router.url.includes('details')) this.router.navigateByUrl('/dashboard/expensives/create');
            } else {
              dialogSmall( 'Cannot delete the expensive.', 'error' );
              // console.log('expensive eliminado fallida');
            }
          } catch (error:any) {
            Swal.fire({
              icon: 'error',
              title: 'Error',
              text: `Error: ${error?.error?.join(', ')}`,
              footer: 'Expensive could not be deleted',
            });
          }
        }
      })
    }
  }

  async getCategoriesExpensives(){
    if(!this.categoriesExpensives?.length && localStorage.user){
      try {
        let response2: any = await this.expensivesSrv.categoriesExpenses(JSON.parse(localStorage.user).person.id);
  
        if(response2.length){
          this.store.dispatch(CategoriesExpenses({any: response2}));
        }else{
          this.store.dispatch(CategoriesExpenses({any: []}));
        }
      } catch (error) {
        console.log('error en categories CategoriesExpenses', error);
        this.store.dispatch(CategoriesExpenses({any: []}));
      }
    }
  }
}
