import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

export const defaultIdError: Promise<any> = new Promise((resolve, reject) => {
  // console.log(resolve, reject);
  reject('There is not ID');
});

@Injectable({
  providedIn: 'root'
})
export class UserService {
  getDolar(): Promise<any> {
    return this.http.get('https://www.datos.gov.co/api/id/32sa-8pi3.json?$select=*&$order=`valor`+DESC&$limit=3&$offset=0&$where=%60vigenciadesde%60%20%3E%3D%20%272023-08-01%27%20AND%20%60vigenciadesde%60%20%3C%20%272023-08-21%27&$$read_from_nbe=true&$$version=2.1').toPromise();
  }

  constructor(private http: HttpClient) { }

  person(id: string){
    console.warn('b->person()');
    if(!id){ 
      console.warn('person()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}persons/${id}`).toPromise();
  }

  accounts(id: number | string, ref?: string){
    console.warn('b->accounts()');
    if(!id){ 
      console.warn('accounts()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}person-accounts/${id}`).toPromise();
  }

  createPayMethod(data: any){
    return this.http.post(`${environment.urlBackend}create-account`, data).toPromise();
  }
}
