import { createReducer, on } from '@ngrx/store';
import { CategoriesInvestments } from '../actions/categories_investments';

export interface State {
  loading: boolean;
  loaded: boolean;
  data: any;
  error: any;
}

const initialState: State = {
  loaded: false,
  loading: false,
  data: [],
  error: {},
};

const _categoriesInvestmentsReducer = createReducer(
  initialState,
  on(CategoriesInvestments, (state, action) => ({
    ...state,
    loading: false,
    loaded: true,
    data: action.any,
    error: {},
  }))
);

export function categoriesInvestmentsReducer(state: State, action: any) {
  return _categoriesInvestmentsReducer(state, action);
}
