import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { IState } from './store';
import { CategoriesExpenses } from './store/actions/categories_expenses';
import { CategoriesInvestments } from './store/actions/categories_investments';
import { LoginSuccess } from './store/actions/user';
import { StoreService } from './services/store.service';
import { ExpensivesService } from './services/expensives.service';
import { InvestmentsService } from './services/investments.service';

@Component({
  selector: 'app-root',
  template: `<router-outlet></router-outlet>`
})
export class AppComponent implements OnInit {
  constructor(
    private store:Store<IState>,
    private storeSrv: StoreService,
    private expensivesService: ExpensivesService,
    private investmentService: InvestmentsService, 
  ){
    if(localStorage.user){
      this.store.dispatch(LoginSuccess({any: JSON.parse(localStorage.user)}));
    }
  }

  async ngOnInit(){
    if(Object.keys(localStorage.length) && localStorage.user){
      try {
        let response: any = await this.expensivesService.categoriesExpenses(JSON.parse(localStorage.user).person.id);
  
        if(response.length){
          this.store.dispatch(CategoriesExpenses({any: response}));
        }else{
          this.store.dispatch(CategoriesExpenses({any: []}));
          console.log('error en categorias gastos');
        }
      } catch (error) {
        this.store.dispatch(CategoriesExpenses({any: []}));
      }
  
      try {
        let response3: any = await this.investmentService.categoriesInvestments(JSON.parse(localStorage.user).person.id);
  
        if(response3.length){
          this.store.dispatch(CategoriesInvestments({any: response3}));
        }else{
          this.store.dispatch(CategoriesInvestments({any: []}));
          console.log('error en categorias investments');
        }
      } catch (error) { 
        this.store.dispatch(CategoriesInvestments({any: []}));
      }
      
      await this.storeSrv.getCategoriesEarnings();
    }
  }
}
