import { createReducer, on } from '@ngrx/store';
import { ICategoryExpense } from 'src/app/interfaces/categories_expenses';
import { CategoriesExpenses } from '../actions/categories_expenses';

export interface State {
  loading: boolean;
  loaded: boolean;
  data: ICategoryExpense[];
  error: any;
};

const initialState: State = {
  loaded: false,
  loading: false,
  data: [],
  error: {},
};

const _categoriesExpensesReducer = createReducer(
  initialState,
  on(CategoriesExpenses,
    (state, action) => ({
      ...state, 
      loading: false, 
      loaded: true,
      data: action.any,
      error: {},
    }),
  )
);

export function categoriesExpensesReducer(state: State, action: any) {
  return _categoriesExpensesReducer(state, action);
}