import { createReducer, on } from "@ngrx/store";
import { IStoreExpensives } from "src/app/interfaces/expensives";
import { resetPageFilters, ExpensivePersonMonth, ExpensivePersonToday, ExpensivePersonWeek, ExpensivePersonYear, ExpensivePersonYesterday, ExpensivesPersonFail, ExpensivesPersonLoad, ExpensivesPersonSuccess, ResetFiltersExpensives, SetFiltersExpensives, SetFiltersExpensivesInitial, updateFiltersPaginatorExpensives, SetShowChartFiltersExpensives, CreateExpensivesSuccess, DeleteExpensivesSuccess, ExpensivesReset } from "../actions/expensives.actions";

const initialState: IStoreExpensives = {
    loaded: false,
    loading: false,
    data: [],
    complete: [],
    error: {},
    quantity: 0,
    pages: 0,
    year: 0,
    month: 0,
    week: 0,
    today: 0,
    yesterday: 0,
    total: 0,
    filters: {
        showChart: false,
    },
};

const _expensivesReducer = createReducer(
    initialState,
    on(ExpensivesReset, (state, action) => initialState ),
    on(ExpensivesPersonLoad,
        (state, action) => {
            return {
                ...state,
                data: [],
                complete: [],
                error: {},
                loaded: false,
                loading: true,
            }
        },
    ),
    on(ExpensivesPersonSuccess,
        (state, action) => {
          let pagesArray = [];
          if(action?.pages){
              for (let i = 0; i < action?.pages; i++) {
                  pagesArray.push(i);
              }
          }
          return {
              ...state,
              data: action.expensives?.rows,
              complete: action?.complete,
              total: action.total,
              page: action.page,
              pages: action.pages,
              limit: action.limit,
              pagesArray,
              quantity: action.quantity,
              loaded: true,
              loading: false,
              filters: {
                  ...state.filters,
                  quantity: action.quantity,
                  total: action.total,
                  page: action.page,
                  pages: action.pages,
                  limit: action.limit,
                  pagesArray,
              }
          }
      },
    ),
    on(ExpensivesPersonFail,
      (state, action) => {
          return {
              ...state,
              data: [],
              complete: [],
              total: 0,
              error: action.error,
              loaded: true,
              loading: false,
          }
      },
    ),
    on(ExpensivePersonYear,
        (state, action) => ({
            ...state,
            year: action?.total
        })
    ),
    on(ExpensivePersonMonth,
        (state, action) => ({
            ...state,
            month: action?.total
        })
    ),
    on(ExpensivePersonWeek,
        (state, action) => ({
            ...state,
            week: action?.total
        })
    ),
    on(ExpensivePersonToday,
        (state, action) => ({
            ...state,
            today: action?.total
        })
    ),
    on(ExpensivePersonYesterday,
        (state, action) => ({
            ...state,
            yesterday: action?.total
        })
    ),

    on(SetFiltersExpensives,
      (state, action) => {
          // console.warn(action.filters);
          const newFilters = { ...action?.filters };

          if(newFilters?.showChart === 'true'){
              newFilters.showChart = true;
          }
          else if(newFilters?.showChart === 'false'){
              newFilters.showChart = false;
          }
          else if(!newFilters?.showChart){
              newFilters.showChart = false;
          }

          return {
          ...state,
          filters: {
              ...state.filters,
              ...newFilters
          }
      }}
  ),

  on(SetFiltersExpensivesInitial,
      (state, action) => {
          // console.warn('set filter initial', action);
          const filters = { ...action?.filters };
          
          if(filters?.showChart === 'true'){
              filters.showChart = true;
          }
          else if(filters?.showChart === 'false'){
              filters.showChart = false;
          }

          return {
          ...state,
              filters: {
                  ...state.filters,
                  ...filters
              }
          }
      }
  ),

  on(ResetFiltersExpensives,
      (state, action) => ({
          ...state,
          filters: {
              showChart: false,
          }
      })
  ),
  
  on(updateFiltersPaginatorExpensives,
      (state, action) => ({
          ...state,
          filters: {
              ...state.filters,
              ...action.filters
          }
      })
  ),

  on(resetPageFilters,
      state => ({
          ...state,
          filters: {
              ...state.filters,
              page: 0,
              pages: 0,
              pagesArray: [],
              quantity: 0
          }
      })
  ),

  on(SetShowChartFiltersExpensives,
      (state, action) => {
          // console.error(state.filters);
          return ({
          ...state,
          filters: {
              ...state.filters,    
              showChart: !state?.filters?.showChart
          }
      })
  }
  ),

  on(CreateExpensivesSuccess,
      (state, action) => ({
          ...state,
          month: 0,
          year: 0,
      })
  ),

  on(DeleteExpensivesSuccess,
      (state, action) => ({
          ...state,
          month: 0,
          year: 0,
      })
  ),
);

export function expensivesReducer(state: IStoreExpensives, action: any) {
    return _expensivesReducer(state, action);
}