import { createAction, props } from "@ngrx/store";
import { IInvestmentsResponse, IFiltersInvestments } from "src/app/interfaces/http/investments-http.type";

// Actions
export const CreateInvestmentSuccess = createAction('[Investment User] Create Success');
export const DeleteInvestmentSuccess = createAction('[Investment User] Delete Success');

// General Data
export const InvestmentsReset = createAction('[Investment User] Reset');
export const InvestmentsPersonLoad = createAction('[Investment User] Load');
export const InvestmentsPersonSuccess = createAction( '[Investment User] Success', props<IInvestmentsResponse>() );
export const InvestmentsPersonFail = createAction( '[Investment User] Fail', props<{error: any}>() );

// Data Global
export const InvestmentsPersonYear = createAction( '[Investment User] Year', props<{total: number}>() );
export const InvestmentsPersonMonth = createAction( '[Investment User] Month', props<{total: number}>() );
export const InvestmentsPersonWeek = createAction( '[Investment User] Week', props<{total: number}>() );

// Filters
export const RestoreFiltersInvestments = createAction( '[Investment User] Restore Filters Investments', props<{filters: IFiltersInvestments, ref?:string}>() );
export const SetFiltersInvestmentsInitial = createAction( '[Investment User] Set Filters Initial', props<{filters: IFiltersInvestments, ref:string}>() );
export const SetFiltersInvestments = createAction( '[Investment User] Set Filters Investments', props<{filters: IFiltersInvestments, ref:string}>() );
export const updateFiltersPaginatorInvestments = createAction( '[Investment User] Update Filters Paginator Investments', props<{filters: IFiltersInvestments, ref:string}>() );
export const resetPageFilters = createAction( '[Investment User] Reset Filters Page Investments' );
export const ResetFiltersInvestments = createAction( '[Investment User] Reset Filters Investments' );
export const SetShowChartFiltersInvestments = createAction( '[Investment User] Set Show Chart Filters Investments' );