import { ActionReducerMap, } from '@ngrx/store';
import { categoriesEarningsReducer } from './reducers/categories_earnings';
import { categoriesExpensesReducer } from './reducers/categories_expenses';
import { categoriesInvestmentsReducer } from './reducers/categories_investments';
import { userReducer } from './reducers/user';
import { earningsReducer } from './reducers/earnings.reducer';
import { expensivesReducer } from './reducers/expensives.reducer';
import { investmentsReducer } from './reducers/investments.reducer';

export interface IState {
  user: any;
  earnings: any;
  investments: any;
  expensives: any;
  categories_expenses: any;
  categories_earnings: any;
  categories_investments: any;
}

export const reducers: ActionReducerMap<IState> = {
  user: userReducer,
  earnings: earningsReducer,
  investments: investmentsReducer,
  expensives: expensivesReducer,
  categories_expenses: categoriesExpensesReducer,
  categories_earnings: categoriesEarningsReducer,
  categories_investments: categoriesInvestmentsReducer,
};
