import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { defaultIdError } from './user.service';
import { environment } from 'src/environments/environment';
import { buildUrl, currentDateInput } from 'src/environments/global';

@Injectable({
  providedIn: 'root'
})
export class ExpensivesService {
  constructor(private http: HttpClient) { }

  expenses(id: string){
    console.warn('b->expenses()');
    if(!id) { 
      console.warn('expenses()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}expensives-person/${id}`).toPromise();
  }

  getExpensesByPayMethod(data: any){
    return this.http.get(`${environment.urlBackend}expensives-payment-method?method=${data.method}&person=${data.person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
  }
  getDataExpense(id: any){
    return this.http.get(`${environment.urlBackend}expensive/${id}`, {}).toPromise();
  }

  getExpensesByDate(data: any, ref: string){
    console.warn('b->getExpensesByDate()');
    if(!data.id_person) { 
      console.warn('getExpensesByDate()');
      return defaultIdError;
    }
    if(!data?.page) data.page = 0;
    if(data?.type === 'today'){ data.date = currentDateInput(new Date()); }

    if(data.pagesArray) delete data.pagesArray;
    if(data.pages) delete data.pages;
    if(data.total) delete data.total;
    if(data.quantity) delete data.quantity;

    const url = buildUrl(`${environment.urlBackend}expensives-date`, data);
    return this.http.get(url, data).toPromise(); }

  getExpensesByCategory(data: any){
    return this.http.get(`${environment.urlBackend}category-expensives?id_category_expensives=${data?.id_category_expensives}&id_person=${data?.id_person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
  }

  createUpdateExpense(id: any, data: any){
    return this.http.post(`${environment.urlBackend}create-update-expensive/${id}`, data).toPromise();
  }

  deleteExpensive(data: any){
    return this.http.post(`${environment.urlBackend}delete-expensive`, data).toPromise();
  }

  categoriesExpenses(id: string){
    console.warn('b->categoriesExpenses()');
    if(!id) { 
      console.warn('categoriesExpenses()');
      return defaultIdError;
    }
    return this.http.get(`${environment.urlBackend}category-expensives/${id}`).toPromise();
  }

  getValuesDataCategories(id: string) {
    return this.http.get(`${environment.urlBackend}category-expensives-values/${id}`).toPromise();
  }

  createCategoryExpense(data: any){
    return this.http.post(`${environment.urlBackend}create-category-expensive`, data).toPromise();
  }

  deleteCategory(data: any){
    return this.http.post(`${environment.urlBackend}delete-category-expensive`, data).toPromise();
  }

}
