import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { buildUrl, currentDateInput } from 'src/environments/global';
import { defaultIdError } from './user.service';

@Injectable({
    providedIn: 'root'
})
export class InvestmentsService {
    constructor(private http: HttpClient) { }

    categoriesInvestments(id: string) {
      console.warn('b->categoriesInvestments()');
      if (!id){ 
        console.warn('categoriesInvestments()');
        return defaultIdError;
      }
      return this.http.get(`${environment.urlBackend}category-investments/${id}`).toPromise();
    }

    getValuesDataCategories(id: string) {
        return this.http.get(`${environment.urlBackend}category-investments-values/${id}`).toPromise();
    }

    investments(id: string) {
      console.warn('b->investments()');
      if (!id){ 
        console.warn('investments()');
        return defaultIdError;
      }
      return this.http.get(`${environment.urlBackend}investments-person/${id}`).toPromise();
    }

    createUpdateInvestment(id: string, data: any) {
        return this.http.post(`${environment.urlBackend}create-update-investment/${id}`, data).toPromise();
    }

    createCategoryInvestment(data: any) {
        return this.http.post(`${environment.urlBackend}create-category-investment`, data).toPromise();
    }

    deleteCategorieInvestment(data: any) {
        return this.http.post(`${environment.urlBackend}delete-category-investment`, data).toPromise();
    }

    deleteInvestment(data: any) {
        return this.http.post(`${environment.urlBackend}delete-investment`, data).toPromise();
    }

    getDataInvestment(id: any) {
        return this.http.get(`${environment.urlBackend}investment/${id}`, {}).toPromise();
    }

    getDataInvestmentHistory(id: any) {
        return this.http.get(`${environment.urlBackend}investment-history/${id}`, {}).toPromise();
    }

    getInvestmentsByDate(data: any, ref: string) {
      console.warn('b->getInvestmentsByDate()');
      if(!data.id_person){ 
        console.warn('getInvestmentsByDate()');
        return defaultIdError;
      }
      if(!data?.page) data.page = 0;
      if(data?.type === 'today'){ data.date = currentDateInput(new Date()); }

      if(data.pagesArray) delete data.pagesArray;
      if(data.pages) delete data.pages;
      if(data.total) delete data.total;
      if(data.quantity) delete data.quantity;

      const url = buildUrl(`${environment.urlBackend}investments-date`, data);
      return this.http.get(url, data).toPromise();
    }

    getInvestmentsByCategory(data: any) {
        return this.http.get(`${environment.urlBackend}category-investments?category=${data?.category}&person=${data?.person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
    }

    getInvestmentsByAccount(data: any) {
        return this.http.get(`${environment.urlBackend}investments-account?account=${data.account}&person=${data.person}${data?.price ? `&price=${data?.price}` : ''}${data?.limit ? `&limit=${data?.limit}` : ''}${data?.start ? `&start=${data?.start}` : ''}${data?.page || data?.page === 0 ? `&page=${data?.page}` : ''}${data?.offset || data?.offset === 0 ? `&offset=${data?.offset}` : ''}`, data).toPromise();
    }
}
